@import url('https://fonts.googleapis.com/css2?family=Amiri&display=swap');

body{
    overflow-x: hidden;

}
body *{
    transition: .5s background-color;

    font-family: "Amiri" !important;

}
.navbar{
    flex-direction: row !important;
    justify-content: space-between;
}

.navbar.light{
    background-color: var(--white);
    color: var(--dark);

}
.navbar.dark{
    background-color: var(--dark-2);
    color: var(--white);
}

.navbar.dark .icon{
    color:rgb(255, 185, 21);
}


.navbar.light .icon-info{
    color:var(--dark-2)
}

.navbar.dark .icon-info{
    color:var(--white)
}


.containerOfList{
    min-height:1000vh;
    width: 100vw;
    position: relative;
}
.containerOfList.light{
    background-color: var(--white-2);
}
.containerOfList.dark{
    background-color: var(--dark);
}

.center{
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
}



.containerOfList.dark .card{
    background-color: var(--dark-2);
    color:var(--white);
}

.containerOfList.dark .player{
    background-color: var(--dark-2) !important;
    color:var(--white-2);
}
.containerOfList.dark .player .controll *{
    color:var(--white) !important;
}

.player{   
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom:0;
    left:0;
    width:100vw;
    max-height: 200px;
    background-color: var(--white);
}




.player .info{
    display: flex;
    cursor: pointer;
    width:66%;
    overflow: auto;

}
.player .controll{
    display: flex;
    margin-right: 3rem;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    
}
.player .controll .icon{
    font-size:3rem;
    color:var(--dark);
}

.search-container input{
    margin:0px 20px !important;
    width:60%;
}
.containerOfList .search-container label{
    margin:0 1em;
  }
  
.containerOfList.light .search-container label{
  color:var(--dark);

}

.containerOfList.dark .search-container label{
    color:var(--white);
  }

.player-lg{
height: 100vh;
width:  100vw;

background-color: var(--dark );

display: flex;
flex-direction: column;
color:rgb(255, 255, 255);
z-index: 1;
position: fixed;
top:0;
left:50%;
transform: translateX(-50%);

animation: fromright .5s;
}

.player-lg  .container-image{

    padding:0 4rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 60%;

}
.player-lg  .container-image img{
    object-fit: cover;
    width:min(80% ,400px);

}
.player-lg .container-controls{
    height: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    width:300px;
    align-self: center;
    
}

.player-lg .container-controls .controls{
    display: flex;
    justify-content: center;

    /* background-color: var(--white); */
    width: 100%;
    height:30%;
}

.player-lg .container-controls .controls .icon{

    color: var(--white);
   font-size: 3rem;
}   

a{
    color:inherit !important;
    text-decoration: inherit !important;
}

.player-lg .icon-back{
    position: absolute;
    top:20px;
    left:20px;
   font-size: 2rem;

}

.player-lg .time{
    display: flex;
    justify-content: space-between;
    width:100%;
}

@keyframes fromright {
    from{
        position: fixed;
        left:100%;

    }
    to{
        position: fixed;

        left:50%;
    }
}


@keyframes frombottom {
    from{
        position: fixed;
        top:100%;

    }
    to{
        position: fixed;
        top:0%;
    }
}




.grid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 1em;
    column-gap: .3em;
  }

.grid div:nth-child(odd){
    grid-row: span 1;
}
.grid div:nth-child(even){
    grid-row: span 2;
    background-color: var(--dark) !important;
}




@media (max-width:800px){
    .grid{
        grid-template-columns: repeat(2,1fr);
    }
    .grid div:nth-child(odd){
        grid-column: span 1;
    }
    .grid div:nth-child(even){
        grid-row: span 1;
       
    }

    .player-lg{
        top:0;
        animation: frombottom .3s;

    }
}
